
import { ref, reactive, onMounted, toRefs } from "vue"
import Skucom from "@/components/Sku.vue"
import * as _ from "lodash"

export default {
  components: {
    Skucom,
  },
  props: {},
  setup(props: any, context: any) {
    const state = reactive<any>({
      tab5value: "0",
      customBySlot: false,
      categoryInfo: {},
      category: [{}],
      categoryChild: [{}],
    })

    onMounted(() => {
      getData()
    })

    const getData = () => {
      fetch("//storage.360buyimg.com/nutui/3x/categoryData.js")
        .then((response) => response.json())
        .then((res) => {
          console.log(res)
          const { categoryInfo, categoryChild } = res
          state.categoryInfo = categoryInfo
          state.category = categoryInfo.category
          state.categoryChild = categoryChild
        })
        .catch((err) => console.log("Oh, error", err))
    }

    const tabClick = (evt: any) => {
      let { title, paneKey, disabled } = evt
      let data =
        state.categoryInfo.category[paneKey + 1] &&
        state.categoryInfo.category[paneKey + 1].children &&
        _.isArray(state.categoryInfo.category[paneKey + 1].children)
          ? state.categoryInfo.category[paneKey + 1].children
          : []
      state.categoryChild = [].concat(data)
    }

    const skuClose = () => {
      state.customBySlot = false
    }

    return {
      ...toRefs(state),
      tabClick,
      skuClose,
    }
  },
}
